import React, { PureComponent } from 'react';
import Toast from '../../components/Toast/Toast';
import { saveSetting, getSetting, shuffle } from '../../service/UtilService';
import { SEQUENCE, SHUFFLE, REPEAT_ONE } from '../../constants/Constants';
import favicon from '../../assets/icons/favicon.png';
import './Player.css';

class Player extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      playType: SEQUENCE,
      isPlaying: false
    };
    this.myAudio = React.createRef();
    this.currentTime = React.createRef();
    this.progressWrapper = React.createRef();
    this.progressBar = React.createRef();
    this.llPlayer = React.createRef();
    this.llPlayPause = React.createRef();
    this.duration = React.createRef();
    this.mouseDown = false;
    this.showCurrentTime = null;
    this.playList = []; // danh sch bi ht s c pht
    this.initPlayList = []; // danh sch ban u,  khi shuffle, xong li chuyn v sequence, th s ly list ny
    this.activeAudio = 0; // Index of current song in playList
  }

  componentDidMount() {
    // truyn instance ca class ny cho component cha,
    //  thng cha c th gi cc function trong class ny
    this.props.getPlayerInstance(this);
  }

  // Hin ti BE ko cn li ko get c duration ca API streaming MP3 na,
  // nn tm thi ko cn show warning
  // componentDidUpdate(prevProps) {
  //   if (this.props.songURL !== prevProps.songURL) {
  //     const interShowDurationIssue = setInterval(() => {
  //       if (this.myAudio && this.myAudio.duration === Infinity) {
  //         Toast.warn('Cannot get duration of this song from Zing');
  //         clearInterval(interShowDurationIssue);
  //       }
  //     }, 1000);
  //   }
  // }

  // Ref: https://developer.mozilla.org/en-US/docs/Web/API/MediaSession
  setMediaSessionMeta = (title, artist, imgUrl) => {
    if (!imgUrl) {
      imgUrl = favicon;
    }

    if ('mediaSession' in window.navigator) {
      window.navigator.mediaSession.metadata = new window.MediaMetadata({
        title: title,
        artist: artist,
        artwork: [
          { src: imgUrl, sizes: '96x96', type: 'image/png' },
          { src: imgUrl, sizes: '128x128', type: 'image/png' },
          { src: imgUrl, sizes: '192x192', type: 'image/png' },
          { src: imgUrl, sizes: '256x256', type: 'image/png' },
          { src: imgUrl, sizes: '384x384', type: 'image/png' },
          { src: imgUrl, sizes: '512x512', type: 'image/png' }
        ]
      });
      window.navigator.mediaSession.setActionHandler('previoustrack', this.prevSong);
      window.navigator.mediaSession.setActionHandler('nexttrack', this.nextSong);
      window.navigator.mediaSession.setActionHandler('play', this.playAudio);
      window.navigator.mediaSession.setActionHandler('pause', this.pauseAudio);
      window.navigator.mediaSession.setActionHandler('stop', this.pauseAudio);
    } else {
      console.log("Your browser doesn't not support MediaSession");
    }
  };

  // Mi ln parent mun i sang pht 1 danh sch khc th parent phi
  // truyn danh sch pht mi  thng qua function ny, v phi setPlayType li,
  // v nu playType = shuffle th cng cn shuffle li ci playList va c
  // truyn vo  (m vic shuffle ny c thc hin trong function setPlayType)
  setPlayList = (pl, activeAudioIdx) => {
    // Note dng nh sau l sai nh:
    // this.playList = pl;
    // y l truyn tham chiu, do  nu sau ny thay i bin this.playList
    // (shuffle n chng hn, th bin pl t component cha cng s thay i theo => hng)
    this.playList = [...pl];
    this.initPlayList = [...pl];
    this.activeAudio = activeAudioIdx;
    this.setPlayType(getSetting('playType'));
  };

  setActiveAudio = (song) => {
    // Nu click Next/Previous button th s ri vo case ny: activeAudio  c update
    //  2 function  ri
    if (this.playList[this.activeAudio].id === song.id) return;

    // Nu click trc tip trn songTable  pht bi mi th ta cn update li activeAudio
    for (let i = 0; i < this.playList.length; i++) {
      if (this.playList[i].id === song.id) {
        this.activeAudio = i;
        break;
      }
    }
    console.log('reset ActiveAudio: ', this.activeAudio);
  };

  setMouseDown = () => {
    this.mouseDown = true;
  };

  clearMouseDown = () => {
    this.mouseDown = false;
  };

  // Trong app ny, vic toggle c p dng vi cc button c 2 state: ON, OFF.
  // Toggle s ly gi tr mi ngc vi gi tr hin ti.
  // Trong cc function toggle s KHNG update state, v s gi cc function abc() khc
  //  bn chng setState, v c th c ch khc cng s gi cc function abc() ,
  // nu update state trong toggle th ni khc gi abc() s ko update state c
  toggleShuffle = () => {
    this.state.playType === SHUFFLE ? this.setPlayType(SEQUENCE) : this.setPlayType(SHUFFLE);
  };

  toggleRepeat = () => {
    this.state.playType === REPEAT_ONE ? this.setPlayType(SEQUENCE) : this.setPlayType(REPEAT_ONE);
  };

  togglePlay = () => {
    this.state.isPlaying ? this.pauseAudio() : this.playAudio();
  };

  prevSong = () => {
    const { playType } = this.state;
    if (playType !== REPEAT_ONE) {
      if (this.activeAudio === 0) {
        Toast.warn('This is the first song of playing list!');
        return;
      } else this.activeAudio--;
    }
    this.props.playSong(this.playList[this.activeAudio], true);
  };

  nextSong = () => {
    const { playType } = this.state;
    if (playType !== REPEAT_ONE) {
      if (this.activeAudio === this.playList.length - 1) {
        Toast.warn('This is the last song of playing list. Please select another song manually to play!');
        this.togglePlay(); // stop playing
        return;
      } else this.activeAudio++;
    }
    this.props.playSong(this.playList[this.activeAudio], true);
  };

  secsToMins = (time) => {
    let int = Math.floor(time),
      mins = Math.floor(int / 60),
      secs = int % 60,
      newTime = mins + ':' + ('0' + secs).slice(-2);

    return newTime;
  };

  getCurrentTime = () => {
    if (!this.myAudio) return;

    const currentTimeFormatted = this.secsToMins(this.myAudio.currentTime),
      currentTimePercentage = (this.myAudio.currentTime / this.getDuration()) * 100;

    this.currentTime.innerText = currentTimeFormatted;
    this.progressBar.style.width = currentTimePercentage + '%';

    if (this.state.isPlaying) {
      this.showCurrentTime = requestAnimationFrame(this.getCurrentTime);
    } else {
      cancelAnimationFrame(this.showCurrentTime);
    }
  };

  showDuration = () => {
    let durationFormatted = this.secsToMins(this.getDuration());
    this.duration.innerText = durationFormatted;
  };

  /**
   * Dng updateProgressBar cho c 2 event onClick v onMouseMove  khi user click v ko,
   * sau  th th n vn tua c ti v tr user th chut
   */
  updateProgressBar = (e) => {
    const totalWidth = this.progressWrapper.offsetWidth;
    const offsetX = e.nativeEvent.offsetX; // https://stackoverflow.com/a/31520106/7688028
    const offsetPercentage = offsetX / totalWidth;

    if (this.mouseDown || e.type === 'click') {
      this.myAudio.currentTime = this.getDuration() * offsetPercentage;
      // Nu isPlaying = true, th vic update time  c thc hin  requestAnimationFrame  trn ri
      if (!this.state.isPlaying) {
        this.getCurrentTime();
      }
      this.props.onSeeked();
    }
  };

  // Nu ko get c duration th API streaming MP3 pha BE b li, cn fix BE.
  // Hin ti th BE cha c li ny, nhng nu chng may dnh th  duration = 10min
  getDuration = () => {
    return this.myAudio.duration === Infinity ? 600 : this.myAudio.duration;
  };

  playAudio = () => {
    let playPromise = this.myAudio.play();

    // In browsers that haven't yet supported this functionality,
    // playPromise won't be defined.
    if (playPromise !== undefined) {
      playPromise
        .then(() => {
          // Automatic playback started!
          this.setState({ isPlaying: true }, () => {
            this.getCurrentTime();
            this.props.onPlay();
          });
        })
        .catch((error) => {
          // Automatic playback failed.
          this.pauseAudio();
          console.log('Error playing file: ', error);
          Toast.error('Li: KHNG th t ng pht nhc, do bn cha tng tc vi trang, hoc Zing khng cho php pht bi ny');
        });
    } else {
      this.pauseAudio();
      Toast.error("Your browser hasn't yet supported this functionality");
    }
  };

  pauseAudio = () => {
    this.myAudio.pause();
    this.props.onPause();
    this.setState({ isPlaying: false });
  };

  // default = SEQUENCE
  setPlayType = (pt = SEQUENCE) => {
    this.setState({ playType: pt });
    if (pt === SHUFFLE) {
      const currSong = this.playList[this.activeAudio];

      // Mi ln chn/chuyn sang pht ngu nhin s trn li danh sch pht
      shuffle(this.playList);

      // find and move the currSong to the head of playList
      for (let i = 0; i < this.playList.length; i++) {
        if (this.playList[i].id === currSong.id) {
          [this.playList[i], this.playList[0]] = [this.playList[0], this.playList[i]]; // swap 2 elements
          this.activeAudio = 0;
          break;
        }
      }

      // console.log(
      //   'After shuffle: ',
      //   this.playList.map((song) => `${song.id}: ${song.title} (${song.artist})`)
      // );
    } else {
      const currSong = this.playList[this.activeAudio];
      this.playList = [...this.initPlayList];

      // find currSong and set activeAudio equal to currSong position
      for (let i = 0; i < this.playList.length; i++) {
        if (this.playList[i].id === currSong.id) {
          this.activeAudio = i;
          break;
        }
      }

      // console.log(
      //   'After sequence: ',
      //   this.playList.map((song) => `${song.id}: ${song.title} (${song.artist})`)
      // );
    }

    saveSetting('playType', pt);
  };

  render() {
    const { song } = this.props;
    const { playType, isPlaying } = this.state;

    return (
      <div
        className={`ll_player ${isPlaying ? 'll-playing' : 'll-paused'}`}
        onMouseDown={this.setMouseDown}
        onMouseUp={this.clearMouseDown}
        ref={(node) => (this.llPlayer = node)}
      >
        <div className="info">
          <div id="song_details">
            <div className="song_title">{song.title}</div>
            <div className="song_artist">{song.artist}</div>
            <div className="song_album">({song.album ? song.album : 'No Album'})</div>
          </div>
        </div>

        <div className="ll-btn-wrapper">
          <button
            className={`ll-shuffle ${playType === SHUFFLE ? 'active' : ''}`}
            title={`${playType === SHUFFLE ? 'Turn off shuffle' : 'Shuffle'}`}
            onClick={this.toggleShuffle}
          >
            <div className="arrow"></div>
            <div className="arrow"></div>
          </button>
          <button className="ll-button ll-btn-prev" title="Previous" onClick={this.prevSong}>
            <div className="arrow arrow-transform"></div>
            <div className="arrow"></div>
          </button>
          <button
            className="ll-button ll-play-pause"
            title={isPlaying ? 'Pause' : 'Play'}
            onClick={this.togglePlay}
            ref={(node) => (this.llPlayPause = node)}
          >
            <div className="arrow arrow-transform"></div>
          </button>
          <button className="ll-button ll-btn-next" title="Next" onClick={this.nextSong}>
            <div className="arrow arrow-transform"></div>
            <div className="arrow"></div>
          </button>
          <button
            className={`ll-repeat ${playType === REPEAT_ONE ? 'active' : ''}`}
            title="Repeat one"
            onClick={this.toggleRepeat}
          ></button>
        </div>

        <div className="time">
          <span className="current-time" ref={(node) => (this.currentTime = node)}>
            0:00
          </span>
          <span
            className="progress"
            ref={(node) => (this.progressWrapper = node)}
            onClick={this.updateProgressBar}
            onMouseMove={this.updateProgressBar}
          >
            <span ref={(node) => (this.progressBar = node)}></span>
          </span>
          <span className="duration" ref={(node) => (this.duration = node)}>
            0:00
          </span>
        </div>

        <audio
          ref={(node) => (this.myAudio = node)}
          id="myAudio"
          preload="metadata"
          src={this.props.songURL}
          onLoadedMetadata={this.showDuration}
          onEnded={this.nextSong}
          crossOrigin="anonymous"
        ></audio>
      </div>
    );
  }
}

export default Player;
